@import url(https://fonts.googleapis.com/css2?family=Forum&family=Libre+Caslon+Text&display=swap);
body {
  margin: 1rem;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
font-family: 'Libre Caslon Text', serif;
color: black;
}

header {
  text-align: center;
}

h1, h2, h3, h4, h5 {
  font-family: 'Forum', cursive;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.weekSummary {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-gap: 8px;
  grid-auto-flow: dense;
  text-transform: lowercase;
  font-size: smaller;
  padding-bottom: 1rem;
}

.categoryHeading {
  background-color: #355C7D;
}

.categoryHeading.category-0 {
  background-color: #F8B195;
}

.categoryHeading.category-1 {
  background-color: #F67280;
}

.categoryHeading.category-2 {
  background-color: #C06C84;
}

.categoryHeading.category-3 {
  background-color: #6C5B7B;
}

.categoryHeading.category-4 {
  background-color: #355C7D;
}

.categoryNews {
  display: grid;
  grid-template-columns: 200px 1fr;
  grid-row-gap:1rem;
  margin-top: 1rem;
}

.categoryNewsArticles {
  border-bottom-style: solid;
  border-bottom-width: 2px;
  border-bottom-color: #355C7D;
}

.category-0 .categoryNewsArticles {
  border-bottom-style: solid;
  border-bottom-width: 2px;
  border-bottom-color: #F8B195;
}

.category-1 .categoryNewsArticles {
  border-bottom-style: solid;
  border-bottom-width: 2px;
  border-bottom-color: #F67280;
}

.category-2 .categoryNewsArticles {
  border-bottom-style: solid;
  border-bottom-width: 2px;
  border-bottom-color: #C06C84;
}

.category-3 .categoryNewsArticles {
  border-bottom-style: solid;
  border-bottom-width: 2px;
  border-bottom-color: #6C5B7B;
}

.category-4 .categoryNewsArticles {
  border-bottom-style: solid;
  border-bottom-width: 2px;
  border-bottom-color: #355C7D;
}


.categoryNews .categoryHeading {
  margin-top: auto;
  margin-bottom: auto;
  height: 100%;
}

.categoryHeadingTitle {
  font-size: 0.8rem;
  display: block;
  position: absolute;
  background-color: white;
  color: lightslategray;
  padding-right: 5px;
  text-transform: uppercase;
  margin-bottom: 0px;
  margin-top: 0px;
}

.categoryHeading div {
  display: block;
  border-bottom-style: solid;
  border-bottom-width: 2px;
  border-bottom-color: white;;
  margin-top: 0.4rem;
}

.categoryNews {
  color: black;
  display: grid;
}

.categoryNews .categoryNewsArticles {
  border-left-style: solid;
  border-left-width: 0px;
  border-left-color: white;
  padding-left: 1rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  grid-column-gap: 1rem;
  -webkit-column-gap: 1rem;
          column-gap: 1rem;
}

.categoryNews article .enclosure {
  display: none;
}

.categoryNews article h1 {
  font-size: 1.3rem;
  margin-top: 0px;
  margin-bottom: 0.5rem;
  overflow-wrap: break-word;
  -webkit-hyphens: auto;
      -ms-hyphens: auto;
          hyphens: auto;
}

.articleSummary {
  font-size: normal;
  
}


.contentSnippet {
  font-size: normal;
  display:none;
}

article .articleInfo {
  width: 100%;
  display: inline-flex;
  flex-direction: row;
  justify-content: space-between;
}

.categoryNews .source {
  font-size: small;
  color: lightslategrey;
  text-transform: uppercase;
}

.categoryNews .category {
  color: lightslategray;
  text-transform: lowercase;
}

.categoryNews article .date {
  font-size: normal;
}

.categoryNews a:link {
  color: black;
  text-decoration: none;
}

.categoryNews a:visited {
  color: black;
  text-decoration: none;
}

.articleCoverContainer {
  width: 100%;
  padding: 0.5rem;
  text-align: center;
}

/* */

.articles {
  color: black;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  grid-gap: 32px;
  grid-auto-flow: dense;
}

.articles article img {
  display: none;
}

.articles h1 {
  font-size: 1.3rem;
  margin-top: 0px;
  margin-bottom: 0.5rem;
  overflow-wrap: break-word;
  -webkit-hyphens: auto;
      -ms-hyphens: auto;
          hyphens: auto;
}

.articles p {
  font-size: normal;
  display:none;
}

article .articleInfo {
  width: 100%;
  display: inline-flex;
  flex-direction: row;
  justify-content: space-between;
}

.articles .source {
  font-size: small;
  color: lightslategrey;
  text-transform: uppercase;
}

.articles .category {
  color: lightslategray;
  text-transform: lowercase;
}

.articles article .date {
  font-size: normal;
}

.articles a:link {
  color: black;
  text-decoration: none;
}

.articles a:visited {
  color: black;
  text-decoration: none;
}

.articles article {
  /*border-bottom-style: solid;
  border-bottom-width: 1px;
  border-bottom-color: lightgray;
  border-right-style: solid;
  border-right-width: 1px;
  border-right-color: lightgray;*/
}

/* Extra-wide grid-posts */
.articles article:nth-child(31n + 1) {
  grid-column: 1 / -1;
}
.articles article:nth-child(16n + 2) {
  grid-column: -3 / -1;
}
.articles article:nth-child(16n + 10) {
  grid-column: 1 / -2;
}

.articles article:nth-child(31n + 1) h1 {
  font-size: 3rem;
}
.articles article:nth-child(16n + 2) h1 {
  font-size: 2rem;
}
.articles article:nth-child(16n + 10) h1 {
  font-size: 2rem;
}

.articles article:nth-child(31n + 1) img {
  display:none;
  object-fit: cover;
  -webkit-filter: opacity(80%) grayscale(50%);
          filter: opacity(80%) grayscale(50%);
}
.articles article:nth-child(16n + 2) img {
  display:none;
  object-fit: cover;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
}
.articles article:nth-child(16n + 10) img {
  display:none;
  object-fit: cover;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
}

.articles article:nth-child(31n + 1) p {
  display:inline;
  display:initial;
}
.articles article:nth-child(16n + 2) p {
  display:inline;
  display:initial;
}
.articles article:nth-child(16n + 10) p {
  display:inline;
  display:initial;
}

/* Single column display for phones */
@media (max-width: 459px) {
  .articles {
    display: flex;
    flex-direction: column;
  }
  .articles article h1 {
    font-size: 1rem;
  }

  .weekSummary {
    font-size: small;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  }

  .categoryNews {
    display: flex;
    flex-direction: column;
  }
  
  .categoryNews .categoryNewsArticles {
    padding-left: 0rem;
    border-left: none;
  }
  .categoryHeading {
    padding-bottom: 1rem;
  }

  .categoryNews article h1 {
    font-size: 1rem;
  }
}
body {
  color: #000;
}
.page-container {
    margin: 0 auto;
    max-width: 60rem;
    padding: 0 1.0875rem 1.45rem;
}

.blog-post {
  border-left-style: solid;
  border-left-width: 1px;
  border-left-color: lightgray;
  padding: 0.5rem;
  padding-left: 1rem;
  padding-right: 1rem;
  margin: 0.5rem;
  border-right-style: solid;
  border-right-width: 1px;
  border-right-color: lightgray;
}

.embedVideo-container {
  text-align: center;
}

.blog-post h1, .blog-post h2, .blog-post h3, .blog-post h4, .blog-post h5 {
  padding-left: 0.5rem;
  margin-left: -1.5rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  margin-top: 0rem;
  margin-bottom: 0.5rem;
  /*padding-right: 0.5rem;
  margin-right: -1.5rem;*/
  background-color: white;
}

.blog-post h1:first-of-type {
  border-bottom-style: solid;
  border-bottom-width: 1px;
  border-bottom-color: lightgray;
  text-decoration: none;
  margin-left: 0px;
  padding-bottom: 0.5rem;
  margin-bottom: 0.5rem;
}

.blog-post-summary {
  display: inline-flex;
  flex-direction: row;
  justify-content: space-between;
}

.blog-post-summary-text {
  font-size: small;
}
.blog-post-summary-image {
  padding-right: 1rem;
}
.blog-post-summary-info {
  display: none;
  font-size: larger;
  text-align: right;
  white-space: nowrap;
}
.blog-post-date {
  font-size: 1.5rem;
  float: right;
  font-weight: normal;
  padding-left: 0.5rem;
  margin-left: 0.5rem;
  border-left: solid;
  border-left-width: 1px;
  border-left-color: lightgray;
  border-bottom: solid;
  border-bottom-width: 1px;
  border-bottom-color: lightgray;
}
.blog-post .gatsby-resp-image-figcaption {
  text-align: center;
  font-weight: lighter;
  font-size: smaller;
}
.article-read-more {
    display: flex;
    justify-content: flex-end;
}
h1 {
  font-size: 2.5rem;
}
h2 {
  font-size: 2rem;
}
a h3 {
    text-decoration: none;
    color: black;
    font-size: 1.5rem;
    margin-bottom: 0.3rem;
}

header {
  margin-top: 1rem;
}

header h1 {
  margin-bottom: 0px;
}

header ul {
  margin-bottom: 0px;
}

header li {
  margin-bottom: 0.5rem;
}

.site-title h1 {
  margin-top: 0rem;
  font-size: 2.5rem;
}
.site-description {
  margin-bottom: 0px;
  text-align: center;
}

.site-title-small h1 {
  font-size: 1.5rem;
}

.site-description-small {
  margin-top: -1rem;
  margin-bottom: 0px;
  font-size: small;
  text-align: center;
  display: none;
}


 .blog-post-content p:first-child:first-letter {
  float: left;
  font-size: 3rem;
  line-height: 3rem;
  padding-top: 0px;
  padding-right: 0.5rem;
  padding-left: 0px;
}

a {
    color: brown;
    text-decoration: none;
  }
  
  a:hover {
    text-decoration: underline;
  }
  
  a:active {
    color: black;
  }
  
  a:visited {
    color: brown;
  }
  
.bordered-title-bar {
    display: inline-block;
    position: relative;
    width: 100%;
    margin-bottom: 1.5rem;
}
.bordered-title-bar h4 {
    font-size: 1rem;
    display: block;
    position: absolute;
    background-color: white;
    padding-right: 5px;
    text-transform: uppercase;
}

.bordered-title-bar div {
    display: block;
    border-bottom-style: solid;
    border-bottom-width: thin;
    margin-top: 2rem;
}

.latest-writings-item {
  min-width:259px;
  margin: 1rem;
  font-size: medium;
  font-size: initial;
  display: flex;
  flex-direction: row;
}

.latest-writings-item-topic {
  align-self: center;
}

.latest-writings-item-image {
  align-self: center;
  padding-right: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.about-me-text {
  margin: 1rem;
}
.about-me-image {
    float: right;
    width: 16%;
    min-width: 96px;
    margin-left: 3rem;
    margin-right: 1rem;
}

.embedVideoIframe {
  object-fit: contain;
  width: 100%;
  min-width: 259px;
}

.gatsby-image-wrapper {
  margin-right: 0.5rem;
 
}

.gatsby-image-wrapper .picture img {
  object-fit: contain;
  height: inherit;
  width: inherit;
}

.editors-choice {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.editors-choice article a {
  color: black;
}
.editors-choice article {
  max-width: 250px;
  padding-left: 1rem;
  padding-right: 1rem;

}

.editors-choice article:last-of-type {
  border-right: none;
}

.archive {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 1rem;
    gap: 1rem;
    grid-auto-flow: dense;
    font-size: normal;
  }

  .article .article-topic {
    text-transform: uppercase;
  }

  .article p {
    margin-bottom: 0px;
  }

  .post-summary {
    display:inline;
    display:initial;
    font-size: smaller;
  }

  .article-read-more {
    display: none;
    float: right;
    font-size: smaller;
  }

  .archive-description {
    margin: 1rem;
  }

  .archive-tags {
    margin: auto;
    width: 100%;
    display: inline-flex;
    flex-direction: row;
    justify-content: center;
  }

  .tag-item {
    width: 128px;
    display: inline-flex;
    flex-direction: column;
    justify-content: center;
  }

  .tag-link {
    text-align: center;
  }

  .archive-article-item {
    margin: 1rem;
    min-width: 259px;
    font-size: smaller;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  
  .archive-article-item:nth-of-type(odd) {
    border-right: 1px solid lightslategray;
    margin-right: 0px;
    padding-right: 2rem;
  }
  
  .archive-article-item:last-of-type {
    border-right: none;
  }

  .archive-article-item-summary {
    display: none;
  }
  
  .archive-article-item-topic {
    align-self: center;
  }
  
  .archive-article-item-image {
    align-self: center;
    padding-left: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  

  /* Single column display for phones */
  @media (max-width: 459px) {
   
    .archive {
      display: flex;
      flex-direction: column;
    }

    
  }

  @media (max-width: 620px) {
    h1 {
      font-size: 1.5rem;
    }
    .site-description {
      font-size: smaller;
    }
    .article .gatsby-image-wrapper {
      display: none;
    }

   
  }

  @media (max-width: 756px) {

  .page-container {
      padding: 0 0.5rem 0.5rem;
  }  
    header {
      margin: 0px;

    }

    header ul {
      margin-bottom: 0px;
    }

    .latest-writings-item {
      margin-right: 0px;
      margin-left: 0px;
      padding-right: 0.5rem;
    }

    .latest-writings-item-image {
      margin-right: 0px;
      margin-left: 0px;
      padding-right: 0.5rem;
    }
    .latest-writings-item-image .gatsby-image-wrapper {
      margin-right: 0px;
      margin-left: 0px;
      padding-right: 0.5rem;
      max-width: 96px;
    }

    .editors-choice {
      flex-direction: column;
    }

    .editors-choice article {
      border-right: none;
      min-width: 100%;
    }
    .archive-article-item:nth-of-type(odd) {
      border-right: none;
      margin-right: 0rem;
      padding-right: 0px;
    }

    .archive {
      display: grid;
      grid-template-columns: auto;
      margin: 0.5rem;
      grid-gap: 0px;
      gap: 0px;
    }

    .archive-article-item {
      margin-left: 0rem;
      margin-right: 0rem;
    }

    .archive-article-item-image {
      padding-left: 0.5rem;
    }

    .blog-post {
      font-size: smaller;
      border-left-style: none;
      border-right-style: none;
      padding-left: 0.5rem;
      padding-right: 0.5rem;
    }
    
    .blog-post-summary {
      flex-direction: column-reverse;
    }

    .blog-post-summary-info {
      text-align: center;
      font-size: larger;
      margin-left: 1rem;
    }

    .blog-post-summary-image {
      display: none;
    }
  }
